.search {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
  position: relative;
}

.inputContainer {
  width: 40rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3.5rem;
  border-radius: 10px;
  position: absolute;
  top: 8rem;
}

.inputForm {
  width: 85%;
  height: 85%;
  position: absolute;
  left: 4rem;
}

.inputText {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  border: none;
  border-radius: 10px;
  height: 100%;
  font-size: 1.4rem;
  color: white;
  padding-left: 0.7rem;
  outline: none;
}

.inputIcon {
  color: white;
  position: absolute;
  left: 1rem;
}

.searchContainer {
  width: auto;
  margin-top: 5rem;
  position: absolute;
  top: 12rem;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
}

@media screen and (max-width: 1500px) {
  .searchContainer {
    grid-template-columns: repeat(6, 1fr);
  }
}

@media screen and (max-width: 1140px) {
  .searchContainer {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (max-width: 760px) {
  .searchContainer {
    grid-template-columns: repeat(2, 1fr);
  }
}

.searchMovie {
  margin: 0.2rem;
  cursor: pointer;
}

.noPoster {
  color: white;
  height: 100%;
  font-size: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
